import React from "react";
import AutoComplete from "material-ui/AutoComplete";
import SelectField from "material-ui/SelectField";
import MenuItem from "material-ui/MenuItem";

const names = [
  "Description",
  "Transcripts",
  "April ",
  "Ralph Hubbard",
  "Omar Alexander",
  "Carlos Abbott",
  "Miriam Wagner",
  "Bradley Wilkerson",
  "Virginia Andrews",
  "Kelly Snyder"
];

class Filters extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      genomesList: [],
      values: []
    };
    this.getGenomes = this.getGenomes.bind(this);
  }

  getGenomes(search) {
    console.log("Getting genomes with: ", search);
    if (search.length > 2) {
      this.sendRequest(
        "/genomes/select?limit=99&query=" + search
      ).then(result => {
        this.setState({
          genomesList: result.results.map(x => {
            return x.organism.scientific_name;
          })
        });
      });
    }
  }

  sendRequest(url) {
    url = "http://gti-es-0.ebi.ac.uk:8080/api" + url;
    console.log("sending request", url);
    return fetch(url).then(a => a.json());
  }
  handleChange = (event, index, values) => this.setState({ values });

  menuItems(values) {
    return names.map(name => (
      <MenuItem
        key={name}
        insetChildren={true}
        checked={values && values.indexOf(name) > -1}
        value={name}
        primaryText={name}
      />
    ));
  }
  render() {
    const { values } = this.state;
    return (
      <div id="Filters">
        <AutoComplete
          hintText="Genome"
          filter={AutoComplete.fuzzyFilter}
          dataSource={this.state.genomesList}
          onUpdateInput={this.getGenomes}
        />

        <SelectField
          multiple={true}
          hintText="Select a name"
          value={values}
          onChange={this.handleChange}>
          {this.menuItems(values)}
        </SelectField>
      </div>
    );
  }
}

export default Filters;



