import React, { Component } from "react";
import TextField from "material-ui/TextField";
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn
} from "material-ui/Table";
import Filters from "./filters";
import "./App.css";
import FloatingActionButton from "material-ui/FloatingActionButton";
import ContentAdd from "material-ui/svg-icons/content/add";
import AutoComplete from "material-ui/AutoComplete";
import Drawer from "material-ui/Drawer";
import MenuItem from "material-ui/MenuItem";
import RaisedButton from "material-ui/RaisedButton";
import Checkbox from "material-ui/Checkbox";
import { List, ListItem } from "material-ui/List";
import Subheader from "material-ui/Subheader";
import SelectField from "material-ui/SelectField";
import { Tabs, Tab } from "material-ui/Tabs";
import LinearProgress from "material-ui/LinearProgress";

const URL = "http://gti-es-0.ebi.ac.uk:8080/api";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      genes: [],
      cols: [],
      rows: [],
      genomesList: [],
      loading: false,
      genomes: [],
      values: [],
      activeCheckboxes: [],
      open: false,
      selectedGenome: "",
      selectedFields: "",
      userSelectedGenome: "",
      searchFields: [
        { name: "Description", id: "description" },
        { name: "Biotype", id: "biotype" },
        { name: "Taxon ID", id: "taxon_id" },
        { name: "Start", id: "start" },
        { name: "Strand", id: "strand" }
      ]
    };
    this.searchGene = this.searchGene.bind(this);
    this.getGenomes = this.getGenomes.bind(this);
    this.searchGeneMain = this.searchGeneMain.bind(this);
    this.handleToggle = this.handleToggle.bind(this);
    this.updateFilters = this.updateFilters.bind(this);
    this.selectGenome = this.selectGenome.bind(this);
  }
  handleToggle = () => {
    this.setState({ open: !this.state.open });
  };
  getGenomes(search) {
    console.log("Getting genomes with: ", search);
    if (search.length > 2) {
      this.sendRequest(
        "/genomes/select?limit=99&query=" + search
      ).then(result => {
        this.setState({
          genomesList: result.results.map(x => {
            return x.organism.scientific_name;
          })
        });
      });
    }
  }

  searchGeneMain() {
    console.log("SEARCHING");
    this.searchGene({}, this.state.userEnteredGene);
  }

  sendRequest(url) {
    url = "http://gti-es-0.ebi.ac.uk:8080/api" + url;
    console.log("sending request", url);
    return fetch(url).then(a => a.json());
  }

  selectGenome(genome) {
    if (genome.trim() !== "") {
      let newrows = this.state.rows.filter(x => {
        if (
          x.genome.toLowerCase() ===
          genome
            .trim()
            .toLowerCase()
            .replace(" ", "_")
        )
          return x;
      });
      this.setState({
        userSelectedGenome: genome,
        rows: newrows
      });
    }
  }

  searchGene(e, value) {
    this.setState({
      loading: true,
      userEnteredGene: value
    });

    let queryObject = {
      name: value
    };

    //let fields = ["name", "description", { homologues: ["division", "name"] }];
    let query = { name: value };
    let fields = ["id", "name", "genome"];
    fields = [...new Set(fields.concat(...this.state.values))];
    this.setState({
      selectedFields: fields
    });
    console.log(this.state.values, JSON.stringify(fields));

    let url =
      URL +
      "/genes/fetch?query=" +
      JSON.stringify(query) +
      '"}&fields=' +
      JSON.stringify(fields);

    console.log("Fetching", url);
    console.log(this.state.selectedFields);
    fetch(url)
      .then(a => a.json())
      .then(json => {
        console.log(json);
        this.setState({
          loading: false,
          genes: json,
          cols: json.fields,
          rows: json.results
        });
        this.selectGenome(this.state.userSelectedGenome);
      });
  }

  componentDidMount() {
    this.searchGene({}, "BRCA2");
  }

  updateFilters = (event, index, values) => {
    let filterString = values.join(",");
    this.setState({ values });
    setTimeout(() => {
      this.searchGene({}, this.state.userEnteredGene);
    }, 100);
  };

  filterItems(values) {
    return this.state.searchFields.map(item => (
      <MenuItem
        key={item.id}
        insetChildren={true}
        checked={values.includes(item.id)}
        value={item.id}
        primaryText={item.name}
      />
    ));
  }

  render() {
    let rows,
      cols = "";

    if (this.state.selectedFields) {
      cols = this.state.selectedFields.map((y, index) => {
        return <TableHeaderColumn key={index}>{y}</TableHeaderColumn>;
      });
    }

    if (this.state.rows) {
      rows = this.state.rows.map(y => {
        return (
          <TableRow key={y.id}>
            <TableRowColumn>{y.id}</TableRowColumn>
            <TableRowColumn>{y.name}</TableRowColumn>
            <TableRowColumn>{y.genome}</TableRowColumn>
            {this.state.values.map((item, index) => (
              <TableRowColumn key={index}>{y[item]}</TableRowColumn>
            ))}
          </TableRow>
        );
      });
    }

    return (
      <Tabs>
        <Tab label="Genes">
          <div>
            <TextField
              style={{ margin: "20px 50px" }}
              defaultValue="BRCA2"
              onChange={this.searchGene}
              hintText="Search genes by name"
            />
            <div />
            <br />
            <div id="config" style={{ display: "flex", margin: "20px" }}>
              <AutoComplete
                style={{ margin: "0 20px 0 0" }}
                hintText="Genome"
                filter={AutoComplete.fuzzyFilter}
                dataSource={this.state.genomesList}
                onUpdateInput={this.getGenomes}
                onNewRequest={this.selectGenome}
              />

              <SelectField
                style={{ margin: "0 20px 0 0" }}
                className="customSwitcherField"
                multiple={true}
                hintText="Select filters"
                value={this.state.values}
                onChange={this.updateFilters}>
                {this.filterItems(this.state.values)}
              </SelectField>

              <FloatingActionButton onClick={this.searchGeneMain}>
                <ContentAdd />
              </FloatingActionButton>
            </div>
            <div className="progress2">
              {this.state.loading3 > 0 && <LinearProgress size={60} />}
            </div>

            <div id="results">
              <div>
                <Table>
                  <TableHeader>
                    <TableRow>{cols}</TableRow>
                  </TableHeader>
                  <TableBody>{rows}</TableBody>
                </Table>
              </div>
            </div>
          </div>
        </Tab>

        <Tab label="Transcripts">
          <TextField
            style={{ margin: "20px 50px" }}
            defaultValue=""
            onChange={this.searchGene}
            hintText="Search genes by name"
          />

          <div id="config" style={{ display: "flex", margin: "20px" }}>
            <AutoComplete
              style={{ margin: "0 20px 0 0" }}
              hintText="Genome"
              filter={AutoComplete.fuzzyFilter}
              dataSource={this.state.genomesList}
              onUpdateInput={this.getGenomes}
              onNewRequest={this.selectGenome}
            />

            <SelectField
              style={{ margin: "0 20px 0 0" }}
              className="customSwitcherField"
              multiple={true}
              hintText="Select filters"
              value={this.state.values}
              onChange={this.updateFilters}>
              {this.filterItems(this.state.values)}
            </SelectField>

            <FloatingActionButton onClick={this.searchGeneMain}>
              <ContentAdd />
            </FloatingActionButton>
          </div>
        </Tab>
      </Tabs>
    );
  }
}

export default App;



